<template>
  <div class="mine-about-page">
    <navigation-bar
      class="abount-nav-bar"
      :pageName="title"
      :border="false"
      @onLeftClick="onBack"
    />

    <div class="contentArea" v-html="detailHtml">
    </div>
      <!-- <div style="width: 99%;text-align: center;background: none;margin: 4rem auto 0px;" ng-if="showMsg == 0">
        <img style="display: block;margin: 0 auto;width: 6rem;height: 5rem;" src="img/noSource.png" alt="">
        暂无相关信息
      </div> -->

  </div>
</template>

<script>

  import {
    getMineAboutTextService
  } from "../../services/mineService";

  import Vue from 'vue';
  import {Toast} from 'vant';
  import NavigationBar from "../title/NavigationBar";
  Vue.use(Toast);

  export default {
    name: "mineAboutage",
    components: { NavigationBar },
    mounted () {
      this.loadAboutText();
    },
    data() {
      return {
        active: 0,
        title: '关于',
        detailHtml: '',
      };
    },

    methods: {
      // 过滤style标签内容
      filterHtml: function (html) {
        var rel = /style\s*?=\s*?([‘"])[\s\S]*?\1/;
        var newHtml = '';
        if (html) {
          newHtml = html.replace(new RegExp(rel,'g'), '');
        }
        return newHtml;
      },
      onBack() {
        history.back();
      },
      // 加载关于信息
      loadAboutText() {
         getMineAboutTextService()
          .then(noteData => {
            let htmlStr = noteData['CONTENT'];
            this.detailHtml = this.filterHtml(htmlStr);
          })
          .catch(error => {
            Toast.fail(error);
          });
      }
    }
  };
</script>

